@use '../base/variables' as var;



.header {
    .top-navbar {
        height: 5rem;
        background-color: rgba($color: #d9d9d9, $alpha: 0.40);
        color: #fff;
    }
    .navbar-toggler i {
        color: #fff;
        font-size: 1.8rem;
    }
    
    .navbar-custom {
        padding: 0;
        height: 12rem;
        background-color: rgba($color: #d9d9d9, $alpha: 0.20);
    }
    
    
    .navbar__navigation ul li {
        margin-right: 3.5rem;
        font-size: 1.6rem;
    }
    .nav-link, .nav-item {
        color: #fff;
        &.active {
            color: #fff !important;
            font-weight:400;
            border-bottom: 2px solid #fff;
        }

        &:hover {
            color: #d9d9d9;
        }
    }
    
    
    .nav-sticky.navbar-custom {
      margin-top: 0px;
      box-shadow: 0px 3px 10px 0px rgba(38, 107, 193, 0.08);
    }
    
    .nav-sticky.navbar-custom.sticky-dark {
        background-color: var.$purple-lighten;
    }
    
    .offcanvas {
        width: var.$offcanvas-horizontal-width;
        .offcanvas-header {
            background-color: var.$purple-darken;
            button {
                i {
                    font-size: 2.8rem;
                }
            }
        }
        .offcanvas-body {
            background-color: var.$purple-darken;
            .navbar-nav {
                .nav-item {
                    font-size: 1.6rem;
                    margin-bottom: 1rem
        
                }; 
            }
        }

    }
}